/* src/components/About.css */

.about-section {
    background-color: #0f172a;
    color: #f1f5f9;
    padding: 80px 20px;
    text-align: center;
  }
  
  .about-container {
    max-width: 900px;
    margin: auto;
  }
  
  .about-section h2 {
    font-size: 2.5rem;
    margin-bottom: 30px;
    color: #38bdf8;
  }
  
  .about-section p {
    font-size: 1.1rem;
    line-height: 1.7;
    margin-bottom: 20px;
  }
  