.top-section {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 80px 60px;
    background: linear-gradient(to right, #0f2027, #203a43, #2c5364);
    color: white;
    min-height: 100vh;
  }
  
  .top-text {
    max-width: 50%;
  }
  
  .top-text h1 {
    font-size: 3.5rem;
    margin-bottom: 0.5rem;
  }
  
  .top-text h2 {
    font-size: 1.8rem;
    margin-bottom: 1rem;
    color: #00ffff;
  }
  
  .top-text p {
    font-size: 1.1rem;
    margin-bottom: 2rem;
    line-height: 1.6;
  }
  
  .cta-button {
    background-color: #00ffff;
    color: #000;
    padding: 12px 30px;
    text-decoration: none;
    border-radius: 6px;
    font-weight: bold;
    transition: transform 0.3s ease;
  }
  
  .cta-button:hover {
    transform: scale(1.05);
    background-color: #0ff;
  }
  
  .top-animation {
    width: 45%;
  }
  
  .lottie {
    width: 100%;
    height: auto;
  }
  